import React, { useEffect, useState } from 'react';

import axios from 'axios'

const App = () => {

  const [ users, setUsers ] = useState([]);

  useEffect(()=> {
    axios.get('https://hahak.herokuapp.com/users')
    .then((result)=> {
      setUsers(result.data)
    })
  }, [])
  return (
    <div>
      Hello, World!!
      {users.map((user, i)=> {
        return <div key={i}>
          {user.nick}
        </div>
      })}
    </div>
  );
}

export default App;
